.about {
	padding-top: 109px;
	padding-bottom: 105px;
}	
.about__description {
	float: right;
	width: 48.7%;
	font-weight: 400;
}
.about__head {
	font-family: 'ChunkFive-Roman';
	font-size: 373%;
	margin: 0 0 46px;
	line-height: 103%;
	@extend %firstSecond;
}
.about__text {
	line-height: 214%;
}
.about__blockImg {
	width: 39%;
	padding-left: 46px;
	margin-top: -10px;
}
.about__img {
	width: 100%;
}
.destination {
	background-color:  #f2fefe;
	// outline: 1px solid red;
	padding-top: 62px;
	padding-bottom: 78px;
	&__description {
		float: left;
		padding-left: 100px;
		padding-bottom: 57px;
		width: 58.4%;
		@media screen and (max-width: 992px) {
			padding-bottom: 10px;
		}
	}
	&__head {
		font-family: 'ChunkFive-Roman';
		font-size: 419%;
		margin: 0 0 43px;
		@extend %firstSecond;
	}
	&__text {
		line-height: 222%;
	}
	&__regulation {
		float: right;
		padding: 28px 108px 3px 28px;
		background: url(../images/seeBasic.png) no-repeat 0 27px;
		color:  #aeaeae;
		@media screen and (max-width: 992px) {
			float: left;
			padding-left: 28px;
			margin-left: 100px;
			margin-bottom: 40px;
		}
	}
	&__search {
	    float: left;
		font-size: 87.5%;
		line-height: 15px;
		margin-left: 100px;
		width: 16%;
		padding: 9px 5px 10px 39px;
		background: url(../images/search.png) no-repeat 9px 10px, #fff;
		color: #b5b5b5;
		border: 1px solid #f5f5f5;
		font-style: italic;
		border-radius: 19px;
		&:focus {
			outline: none;
			border: 1px solid $darkgreen;
		}
	}
	&__allPlace {
		float: right;
		margin-right: 101px;
		margin-top: 6px;
		.first {
			font-weight: 600;
		}
	}
	.place {
		margin-top: 18px;
	}
	.place__blockVideo {
		position: relative;
		float: left;
		width: 640px;
		// outline: 1px solid red;
		&:after {
			content: "";
			position: absolute;
			top: 171px;
			right: 0;
			// display: block;
			border: 10px solid transparent;
			border-right: 10px solid #f4f9fb;
		}
		@media screen and (max-width: 992px) {
			float: none;
			&:after {
				content: none;
			}
		}
	}
	.place__shadow {
		position: absolute;
		right: 0;
		bottom: 0;
		color: #fff;
		text-align: right;
	}
	.place__shadow__text {
		position: absolute;
		right: 0;
		bottom: 40px;
		padding-right: 30px;
		background: url('../images/arrowRight.svg') no-repeat 70px 5px;
	}
	.place__shadow__img {
		width: 100%;
		opacity: 0.33;
	}
	.place__about {
		overflow: hidden;
		// outline: 1px solid red;
		padding-top: 52px;
		padding-left: 38px;
		padding-bottom: 60px;
		padding-right: 60px;
		background-color: #f4f9fb;
	}
	.place__about__head {
		color: $darkgreen;
		font-size: 30px;
		margin-left: -4px;
		margin-bottom: 9px;
		padding-left: 4px;
		padding-top: 113px;
		// outline: 1px solid red;
	}
	.place__about__head__batu {
		background: url('../images/batuIcon.svg') no-repeat;
	}
	.place__about__head__bromo {
		padding-top: 72px;
		background: url('../images/bromoIcon.svg') no-repeat;
	}
	.place__about__parth {
		margin: 0;
		line-height: 219%;
	}
	.place__blockImg {
		position: relative;
		float: left;
		width: 57.26%;
		overflow: hidden;
		// outline: 1px solid red;
	}
	.place__img,
	.place__shadow__img {
		margin-bottom: -5px;
	}
	.place_left {
		padding-top: 20px;
		@extend %clearfix;
	}
	.place__about_left {
		box-sizing: border-box;
		position: absolute;
		top: 0;
		left: 0;
		background-color: rgba(255,255,255,.96);
		width: 49.45%;
		padding-top: 43px;
		padding-left: 29px;
		padding-right: 20px;
		&:after {
			content: "";
			position: relative;
			top: 171px;
			left: 0px;
			// display: block;
			border: 10px solid transparent;
			border-left: 10px solid #f4f9fb;
		}
		@media screen and (max-width: 992px) {
			position: static;
			width: 100%;
		}
	}
	.place__blockImg_right {
		float: right;
		width: 40.17%;
	}
	.pagesNumber {
		font-family: 'ChunkFive-Roman';
		font-size: 114%;
		padding-top: 76px;
		text-align: center;
	}
	.pagesNumber__item {
		display: inline-block;
		padding: 0 8px 0 10px;
	}
	.pagesNumber__items {
		margin-left: 46px;
	}
	.pageNumber_active {
		font-size: 134%;
		color: $green;
	}
	.pagesNumber__link {
		color: $gray;
		text-decoration: none;
	}
	.pagesNumber_arrowOne,
	.pagesNumber_arrowTwo {
		// display: inline-block;
		width: 16px;
		height: 18px;
		& .pagesNumber__link {
			display: inline-block;
		    width: 14px;
			height: 14px;
			// outline: 1px solid red;
		}
	}
	.pagesNumber_arrowOne {
		background: url("../images/arrowOneTwo.png") no-repeat 6px -2px;
	}
	.pagesNumber_arrowTwo {
		background: url("../images/arrowOneTwo.png") no-repeat 6px -21px;
		margin-left: -12px;
	}
}
.setIttinery {
	padding-top: 72px;

}
.setIttinery__head {
	font-family: 'ChunkFive-Roman';
	text-align: center;
	font-size: 417%;
	margin: 0 0 46px;
	line-height: 103%;
	@extend %firstSecond;
}
.setIttinery__text {
	text-align: center;
	line-height: 222%;
	padding-bottom: 133px;
}
.setIttinery__formAndButton {
	float: left;
	width: 57.26%;
	border-radius: 5px;
	overflow: hidden;
}
.setIttinery__contantLeft {
	background: #ffffa6;
	padding-bottom: 92px;
}
.setIttinery__headDestinations{
	font-size: 111%;
	font-weight: 900;
	color: #fff;
	height: 74px;
	background: $green url("../images/mountainIttinery.svg") 145px -24px no-repeat;
	text-align: center;
	margin: 0 0 23px;
	line-height: 74px;
}
.setIttinery__item {
	margin: 0 30px 17px 41px;
}
.setIttinery__dayTime {
	margin: 20px 30px 10px 41px;
}
.setIttinery__day {
	float: left;
	color: $green;
	font-weight: 900;
}
.setIttinery__currentDate {
	position: relative;
	text-align: right;
	padding-right: 30px;
	color: $green;
	font-weight: 900;
	&:after {
		position: absolute;
		top: 8px;
		right: 11px;
		content: '';
		border: 5px solid transparent;
		border-top: 7px solid $green;
	}
}
.setIttinery__nameDate {
	border-radius: 5px;
	border-style: solid;
	border-width: 1px;
	border-color:  #d5d57d;
	background-color:  #ffffff;
	width: 100%;
	height: 70px;
	text-align: right;
	position: relative;
	z-index: 1;
	@media screen and (max-width: 900px) {
		font-size: 68%;
	}
}
.setIttinery__name {
	float: left;
	font-size: 150%;
	font-weight: 300;
	padding-left: 99px;
	line-height: 70px;
}
.setIttinery__name_bromo {
	background: url("../images/bromoIcon.svg") no-repeat 10px 20px;
	background-size: 30% auto;
}
.setIttinery__name_batu {
	background: url(../images/batuIcon.svg) no-repeat 20px 9px;
	background-size: 23% auto;
}
.setIttinery__date {
	font-size: 112%;
	font-weight: 300;
	padding-right: 26px;
	line-height: 70px;
}
.setIttinery__subDestination {
	position: relative;
	top: -5px;
	margin: 0 10px 60px 20px;
	background-color: rgba(244, 231, 157, 0.9);
	color: #d8cb7d;
	border-radius: 5px;
}
.setIttinery__add {
	box-sizing: border-box;
	position: relative;
	top: -5px;
	float: right;
	width: 197px;
	border-radius: 5px;
	background-color: rgba(244, 231, 157, 0.9);
	font-size: 100%;
	font-weight: 800;
	text-align: center;
}
.setIttinery__link {
	display: block;
	line-height: 250%;
	padding: 12px 27px 0 0;
	text-decoration: none;
	color: #d8cb7d;
	background: url('../images/iconPlus.svg') no-repeat 163px 20px;
}
.setIttinery__subDetinationItem {
	margin: 0 17px;
	padding: 12px 9px 15px;
	border-top: 1px solid #d8cb7d;
	&:first-child {
		padding-top: 23px;
	}
}
.setIttinery__item_dragAndDrop {
	color: #d8cb7d;
	text-align: center;
	background-color:  rgba(245, 233, 158, 0.41);
	line-height: 70px;
	border-radius: 5px;
	border: 1px dashed #d8cb7d;
}
.button {
	float: right;
	border-radius: 30px;
	line-height: 64px;
	margin-top: 17px;
	margin-left: 25px;
	padding: 8px 24px 0 56px;
	border: none;
	text-transform: uppercase;
	color: #fff;
	cursor: pointer;
}
.setIttinery__button_saveAsPdf {
	padding: 8px 24px 0 56px;
	// background-color: rgba(189, 242, 113, 0.9);
	background: rgba(189, 242, 113, 0.9) url('../images/icon_print-SaveAsPdf.png') 24px -25px no-repeat;
}
.setIttinery__button_print {
	background: rgba(40, 215, 193, 0.9) url("../images/icon_print-SaveAsPdf.png") 26px 28px no-repeat;
}
.setIttinery__listDestinations {
	float: right;
	width: 40.17%;
	// outline: 1px solid red;
}
.setIttinery__headList {
	text-align: center;
	color: $green;
	margin-top: 26px;
	&:after {
		content: '';
		display: block;
		margin: 13px auto 0;
		width: 14.25%;
		height: 3px;
		background-color: $green;
	}
}
.setIttinery__sort {
	margin-top: 37px;
}
.setIttinery__sortBy {
	color: $green;
}
.setIttinery__category {
	position: relative;
	color: #bcbcbc;
	&:after {
		content: '';
		position: absolute;
		top: 9px;
		right: -16px;
		border: 5px solid transparent;
		border-top: 7px solid #bcbcbc;
	}
}
.setIttinery__iconZoom {
	float: right;
	width: 14px;
	height: 14px;
	background: url(../images/search.png) no-repeat 0px 0px;
	background-size: cover;
	margin-top: 18px;
	margin-right: 28px;
}
.setIttinery__listItem {
	height: 70px;
	border-radius: 5px;
	border: 1px solid #e8e8e8;
	margin-top: 10px;
	padding: 0 10px 0 20px;
	color: #b9b9b9;
	background: url("../images/bromoIcon.svg") no-repeat 123% 14px;
	background-size: 45% 45%;
	@media screen and (max-width: 900px) {
	background: url("../images/bromoIcon.svg") no-repeat 100% 14px;
	background-size: 30% 30%;
	}
}
.setIttinery__listName {
	font-size: 150%;
	font-weight: 300;
	padding: 10px 0 10px;
}
.setIttinery__estimatedTime {
	font-size: 87.5%;
}
.setIttinery__valueHours {
	color: $green;
}
.setIttinery__details {
	float: right;
	font-size: 87.5%;
	color: $green;
	// margin-top: 50px;
}
.contact {
	position: relative;
	margin-top: 100px;
	padding-bottom: 73px;
	// outline: 1px solid blue;
}
.map {
	width: 100%;
	position: absolute;
	z-index: -1;
}
.map__colorBlock {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
	background-color: $darkgreen;
	opacity: .8;
}
.contact__head {
	font-family: 'ChunkFive-Roman';
	text-align: center;
	font-size: 417%;
	margin: 84px 0 96px;
	line-height: 103%;
	color: #fff;
}
.contact__wrapper {

}
.form {
	float: left;
	width: 40.17%;
	// outline: 1px solid red;
}
.form__email,
.form__company,
.form__subject,
.form__message {
	margin-bottom: 20px;
	box-sizing: border-box;
	display: block;
	border: 1px solid $lightgray;
	border-radius: 5px;
	padding: 0 20px;
	height: 55px;
}
.form__email {
	width: 100%;
}
.form__company {
	float: left;
	width: 47.87%;
}
.form__subject {
	float: right;
	width: 47.87%;
}
.form__message {
	width: 100%;
	height: 96px;
	padding-top: 20px;
}
.form__button {
	padding: 4px 15px 0;
	margin-top: 10px;
	background-color: rgba(189, 242, 113, 0.9);
	font-size: 100%;
}
.or {
	float: left;
	width: 19.66%;
	height: 334px;
	// outline: 1px solid red;
	text-align: center;
	text-transform: uppercase;
}
.or__text {
	position: relative;
	top: 50%;
	left: 0;
	color: #fff;
}
.youcanfindushere {
	float: right;
	width: 40.17%;
	// outline: 1px solid red;
}
.youcanfindushere__head {
	color:  #ffffff;
	font-size: 44.22px;
	font-weight: 300;
	margin: 0 0 4px;
	padding: 0;
	line-height: 93%;
}
.youcanfindushere__items {
	list-style: none;
	margin: 58px 0 0;
	padding: 0;
	color: #fff;
	font-size: 75%;
}
.youcanfindushere__item {
	margin-bottom: 15px;
	padding-left: 26px;
}
.youcanfindushere__item__second {
	margin-top: 4px;
	color: yellow;
	font-style: italic;
}
.youcanfindushere__item_iconHouse {
	background: url('../images/home_persone.png') 0 0 no-repeat;
}
.youcanfindushere__item_iconPersone {
	background: url('../images/home_persone.png') 0 -56px no-repeat;
}
.keepintouch {
	background-color: #bdf271;
	color: #fff;
	padding-bottom: 24px;
}
.keepintouch__description {
	float: left;
}
.keepintouch__form {
	float: right;
	width: 46.23%;
	margin-top: 65px;
	@media screen and (max-width: 992px) {
		float: none;
		margin-left: -60px;
		// width: 80%;
		// margin-top: 20px;
	}
}	
.keepintouch__input {
	box-sizing: border-box;
	float: right;
	display: block;
	width: 73.87%;
	height: 72px;
	border-radius: 5px 0 0 5px;
	border: 1px solid $gray;
	border-width: 1px 0 1px 1px;
	padding: 0 30px;
	font-size: 90%;
}
.keepintouch__button {
	box-sizing: border-box;
	float: right;
	display: block;
	width: 13.12%;
	height: 72px;
	border-radius: 0 5px 5px 0;
	border: 1px solid $gray;
	border-width: 1px 1px 1px 0;
	padding: 0;
	background: $darkgreen url('../images/arrowWhite.png') 57% 50% no-repeat;
	cursor: pointer;
}
.keepintouch__head {
	font-family: 'ChunkFive-Roman';
	// text-align: center;
	font-size: 417%;
	margin: 62px 0 5px;
	line-height: 103%;
	color: #fff;
}
.keepintouch__text {
	color: #fff;
	margin-bottom: 33px;
}
.footer {
	height: 273px;
	background: #2f2933 url('../images/mountainFooter.svg') 25% 67px no-repeat;
	padding-top: 25px;
	// background-size: 80%;
}
.terms {
	margin: 0;
	padding: 0;
	font-size: 75%;
}
.term {
	float: left;
	position: relative;
	margin-left: 10px;
	padding-left: 13px;
	list-style: none;
	&:first-child {
		padding-left: 0;
		margin-left: 0;
	}
	&:before {
		content: '';
		display: block;
		width: 5px;
		height: 5px;
		border-radius: 2.5px;
		background-color: #5d5b5b;
		position: absolute;
		top: 5px;
		left: 0;
	}
	&:first-child:before {
		content: none;
	}
}
.term__link {
	text-decoration: none;
	color: #5d5b5b;
	&:hover {
		color: #bdf271;
	}
}
.social {
	float: right;
	margin: 0;
	padding: 0;
	list-style: none;
}
.social_item {
	float: right;
}
.fa-circle {
	color: #3d3741;
}
.fa-stack:hover .fa-circle {
	color: #bdf271;
}
.fa-facebook,.fa-twitter,.fa-instagram {
	color: #2f2933;
}
.copyright {
	color: #aeaeae;
	font-size: 75%;
	margin: 200px auto 0;
	text-align: center;
}
