.header {
	position: relative;
	box-sizing: border-box;
	// max-width: 1440px;
	height: 729px;
	margin: 0 auto;
	padding-top: 2.9%;
	background-color: $limegreen;
	background-image: url(../images/mounth.png), url(../images/gradient-menu.png);
	background-repeat: no-repeat, repeat-x;
	background-position: 50% 100%, 0% 0%;
	// background-size: cover, auto auto;

.downOverMounth {
	position: absolute;
	bottom: 0;
	width: 100%;
	max-width: 1290px;
	height: 5.76%;
	background-color: rgba(255, 255, 255, 0.9);
	// margin-top: -42px;
	margin-left: -60px;
	// padding: 0 60px;
}
.logo {
	float: left;
	font-size: 166%;
	padding-left: 3px;
	padding-top: 9px;	
	.logo__link {
		text-decoration: none;
		color: #fff;
		transition: all 1s;	
		&:hover {
			color: $darkgreen;
		}
	}
	.firstParth {
		font-family: 'Montserrat';
	}
	.secondParth {
		font-family: 'Montserrat';
		font-weight: 600;

	}
}
.avatar {
	position: relative;
	float: right;
	margin-right: 15px;
	width: 44px;
	height: 44px;
	transition: all 1s;
	.avatar__img {
		width: 100%;
		border-radius: 50%;
	}
	&:after {
		content: " ";
		position: absolute;
		top: 18px;
		left: 50px;
		border: 4px solid transparent;
		border-top: 7px solid #fff;	
		transition: all 1s;		
	}
	&:hover {
		@include transform(scale(1.2));
		&:after {;
			border-top: 7px solid $darkgreen;			
		}
	}

}
.menu {
	font-family: 'Lato';
	font-weight: 600;
	float: right;
	margin: 0 35px 150px 0;
	padding: 0;
	width: 70%;
	@media screen and (max-width: 992px) {
		width: 100%;
		margin-top: 20px;
		margin-right: 0;
		margin-bottom: 100px;
	}
	// line-height: .3em;
	.menu__items {
		margin: 0;
		padding: 0;
	}
	.menu__item {
		float: right;
		list-style: none;
		padding-left: 23px;
		@media screen and (max-width: 992px) {
			padding-left: 0;
			width: 20%;
			text-align: center;
		}
	}
	.menu__item__link {
		box-sizing: border-box;
		text-decoration: none;
		color: #fff;
		font-size: 91.5%;
		line-height: 257.5%;
		padding: 12px 20px;
		width: 20%;
		&.active {
			border: 3px solid #fff;
			border-radius: 25px;
			&:hover {
				border: 3px solid $darkgreen;
				border-radius: 25px;
				padding: 12px 20px;
			}
		}
		&:hover {
			border: 3px solid $darkgreen;
			border-radius: 25px;
			padding: 12px 17px;
		}
	}
	@media screen and (max-width:749px) {
		margin: 20px 0 150px;
		width: 100%;
	}
}
	.head {
		font-family: 'ChunkFive-Roman';
		text-align: center;
		font-size: 500%;
		margin: 0;
		line-height: 103%;
		.first {
			color: #fff;
		}
		.second {
			color: $darkgreen;
		}
	}
	.slogan {
		text-align: center;
		color: #fff;
		font-size: 111%;
		line-height: 197%;
		margin-bottom: 40px;
	}
	.header__button {
		font-family: 'Lato';
		font-weight: 900;
		text-align: center;
		display: block;
		margin: 0 auto;
		background-color:  #ffffa6;
		width: 18.5%;
		height: 74px;
		line-height: 74px;
		border: 0px;
		border-radius: 37px;
		color:  #000;
		&:hover {	
			cursor: pointer;
			background: #fff;
			color: $darkgreen;
		}
		@media screen and (max-width: 992px) {
			// font-size: 75%;
			padding: 0 5%;
			width: 25%;
		}
	}
}
